<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <img class="banner-img" :src="bannerImg"
        alt="">
      <div class="banner-info">
        <div class="banner-title">{{this.sightName}}</div>
        <div class="banner-number"><span class="iconfont banner-icon">&#xe692;</span>{{this.bannerImgs.length}}</div>
      </div>
    </div>
    <common-gallary :imgs="bannerImgs" v-show="showGallary" @close="handleGallaryClose"></common-gallary>
  </div>

</template>
<script>
  import CommonGallary from 'common/gallary/Gallary.vue'
  export default {
    name: "DetailBanner",
    props:{
      sightName:String,
      bannerImg:String,
      bannerImgs:Array,
    },
    data() {
      return {
        showGallary: false
      }
    },
    components: {
      CommonGallary
    },
    methods: {
      handleBannerClick() {
        this.showGallary = true;
      },
      handleGallaryClose() {
        this.showGallary = false;
      }
    },
  }

</script>
<style lang="stylus" scoped>
  .banner {
    overflow: hidden;
    height: 0;
    padding-bottom: 55%;
    position: relative;
  }

  .banner-img {
    width: 100%;
  }

  .banner-info {
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    line-height: 1.6rem;
    color: #fff;
    background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8))
  }

  .banner-title {
    flex: 1;
    font-size: .8rem;
    padding: 0 .2rem;

  }

  .banner-number {
    padding: .4rem;
    height: .32rem;
    line-height: .32rem;
    margin-top: .4rem;
    border-radius: .2rem;
    background-color: rgba(0, 0, 0, 0.8);
    font-size: .24rem;
  }

  .banner-icon {
    font-size: .24rem;

  }

</style>



